

.Electronics {

  .wrapper {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 1%;

    height: 100%;
    display: flex;
    align-items: start;

    .menu {
      padding: 20px 30px 50px 20px;
      padding-bottom: 50px;
      margin-right: 50px;
      margin-top: 50px;
      height: 100%;
      background-color: dodgerblue;
      color: white;
      border-radius: 15px;

      h2 {
        font-size: 40px;
      }

      nav {
        display: flex;
        flex-direction: column;
      }

      a {
        font-size: 30px;
        text-decoration: none;
        margin-left: 50px;
        color: white;
        padding: 5px 10px;
        border-radius: 10px;

        &:not(last-of-type) {
          margin-bottom: 10px;
        }
      }

      a.active {
        background-color: cornflowerblue;
      }
    }

    .content {
      display: flex;
      //width: 700px;
      //height: 450px;
      margin-top: 50px;
      align-content: stretch;
      flex-wrap: wrap;

      div {
        width: 100%;
        height: 100%;


        img {
          width: 100%;
          height: 100%;
          border-radius: 15px;
        }
      }
    }
  }
}
