
.Header {

  .wrapper {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 1%;


    display: flex;
    align-items: center;
    justify-content: space-between;


    p {
      font: 30px "Poppins-ExtraBold";
      text-transform: uppercase;
    }

    nav {
      display: flex;
      gap: 20px;

      a {
        padding: 5px 15px 5px;

        text-decoration: none;
        color: black;
        border-radius: 8px;
        transition: all .5s;
        font: 25px "Poppins-regular";

        &:hover {
          color: white;
        }

        &:hover[href *= "/nature"] {
          background-color: limegreen;
        }

        &:hover[href="/cities"] {
          background-color: gray;
        }

        &:hover[href="/peoples"] {
          background-color: #d7889f;
        }

        &:hover[href="/electronics"] {
          background-color: cornflowerblue;
        }
      }

      .active {
        color: white;
      }

      .active[href *= "/nature"] {
        background-color: green;
      }

      .active[href="/cities"] {
        background-color: black;
      }

      .active[href="/peoples"] {
        background-color: palevioletred;
      }

      .active[href="/electronics"] {
        background-color: dodgerblue;
      }
    }
  }
}